@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";
@import "../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}































































.calendar-base {
  &__title {
    display: flex;
    align-items: center;
    justify-content: center;
    height: $cellHeight;
    border-bottom: 1px solid $colorElmentUIGray200;
    text-align: center;
  }

  &__text {
    display: inline-block;
    padding: 8px 10px 0;
    height: $cellHeight;
  }

  &__body {
    width: 100%;
    table-layout: fixed;
  }

  &__cell {
    height: $cellHeight;
    color: $colorElmentUIGray600;
    vertical-align: middle;
    text-align: center;
    font-size: 12px;
  }

  &__body-cell {
    padding-top: 2px;
    padding-bottom: 2px;
  }
}
