@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";
@import "../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}






































.input {
  padding: 0 8px;
  width: 100%;
  height: $formPartsHeight - 6;
  border: 1px solid $colorClear;
  border-radius: $sizeRadius;
  background-color: $colorWhite;
  color: $colorText;
  text-align: center;
  font-size: $basicFontSize;
  line-height: $formPartsHeight - 6;
  transition: background-color 0.3s ease;
  pointer-events: none;

  &:hover {
    background-color: $colorBlue600;
    cursor: pointer;
  }

  &::placeholder {
    color: $colorBase700;
  }

  &:disabled {
    border-color: $colorBase500;
    background-color: $colorBase300;
    color: $colorBase600;
  }

  &--focus {
    background-color: $colorBlue600;
  }
}
