@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";
@import "../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}





















































































































































































































































































































































































.date-picker-range-change-input {
  position: relative;

  &__container {
    display: flex;
    align-items: center;
    padding: 2px;
    height: $formPartsHeight;
    border: 1px solid $colorBase600;
    border-radius: $sizeRadius;

    &--foucs {
      border-color: $colorBlue800;
    }
  }

  &__icon {
    width: 25px;
    text-align: center;
  }

  &__field {
    flex: 1;
  }

  &__hyphen {
    padding: 0 10px;
  }

  &__delete-button {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 5px;
    margin-left: 5px;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    cursor: pointer;

    &-icon {
      transform: scale(1);
    }
  }

  &__popup {
    position: absolute;
    top: 40px;
    min-width: 700px;
    width: 100%;
    background-color: $colorWhite;
    box-shadow: 0 1px 4px rgba(#000, 0.1);

    &--left {
      left: 0;
    }

    &--center {
      left: 50%;
      transform: translateX(-50%);
    }

    &--right {
      right: 0;
    }
  }

  &__header {
    padding: 10px;
    border-bottom: 1px solid $colorBase400;
  }

  &__body {
    padding: 10px;
  }
}

.datePickerPopup-enter-active,
.datePickerPopup-leave-active {
  margin-top: 0;
  opacity: 1;
  transition: opacity 0.3s ease, margin-top 0.3s ease;
}
.datePickerPopup-enter,
.datePickerPopup-leave-to {
  margin-top: -40px;
  opacity: 0;
}
